.menu {
  display: none;
  &_active {
    text-transform: uppercase;
    width: 100%;
    top: 90px;
    display: flex;
    position: fixed;
    flex-direction: column;
  }
}
.a_menu_header {
  font-size: 20px;
  color: white;
  text-shadow: 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black;
  &:hover {
    transition: 0.2s;
    color: #0082e6;
  }
}

.li_menu_header {
  margin-bottom: 50px;
  overflow-wrap: break-word;
}
ul::-webkit-scrollbar {
  display: none;
}
.ul_menu_header {
  display: grid;
  grid-template-columns: auto;
  justify-items: center;
  margin-top: 50px;
  &_item {
    display: grid;
    grid-template-columns: auto;
    justify-items: center;
  }
}

.arrow-icon_menu {
  color: dimgray;
  background-color: transparent;
  border: none;
  transition: all 0.3s ease;
  cursor: pointer;
  font-size: 28px;
}
.menu_button_back {
  position: absolute;
  display: flex;
  left: 25%;
  top: 50%;
  overflow-y: visible;
}
.arrow-icon_menu_scroll {
  display: none;
  &_down {
    display: none;
  }
}
.menu_button_scroll {
  position: absolute;
  top: 8px;
}
.menu_button_scroll_down {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  top: 4px;
}
@media (max-width: 320px) {
  .li_menu_header {
    width: 142px;
    justify-items: center;
  }
}
@media (max-width: 426px) {
  .li_menu_header {
    margin-bottom: 50px;
    text-align: center;
  }
  .a_menu_header {
    font-size: 16px;
  }
  .menu_button_back {
    display: grid;
    left: 10%;
  }
  .arrow-icon_menu {
    position: relative;
    left: 10px;
    font-size: 20px;
  }
}

@media (max-height: 712px) {
  .arrow-icon_menu_scroll {
    display: grid;
    color: dimgray;
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 28px;
    &_down {
      display: grid;
      position: relative;
      right: 0px;
      color: dimgray;
      background-color: transparent;
      border: none;
      cursor: pointer;
      font-size: 28px;
    }
  }
  .ul_menu_header {
    max-height: 480px;
    overflow-y: scroll;
  }
}

@media (max-height: 663px) {
  .ul_menu_header {
    max-height: 388px;
  }
}

@media (max-height: 572px) {
  .ul_menu_header {
    max-height: 314px;
  }
}

@media (max-height: 495px) {
  .ul_menu_header {
    max-height: 168px;
  }
}
@media (max-height: 495px) {
  .ul_menu_header {
    max-height: 248px;
  }
}
@media (max-height: 438px) {
  .ul_menu_header {
    max-height: 198px;
  }
}
@media (max-height: 374px) {
  .ul_menu_header {
    max-height: 98px;
  }
}
@media (min-width: 1346px) {
  .ul_menu_header {
    display: none;
  }
}
