.navBarDropBar {
  display: flex;
}
.navdrop_show {
  position: absolute;
  background-color: white;
  width: 100.1%;
  padding-top: 54px;
  left: 0px;
  border: 1px solid black;
}
.navdrop_notvisible {
  display: none;
}
.navdrop {
  cursor: pointer;
  &_notvisible {
    display: none;
  }
}
.navdrop_p {
  color: rgb(203, 203, 203);
  text-shadow: 0 0 1.7px black, 0 0 1.7px black, 0 0 1.7px black, 0 0 1.7px black;
  padding-top: 6px;
}
.navdrop_content {
  display: flex;
  justify-content: space-between;
  padding-left: 90px;
  padding-right: 94px;
  padding-bottom: 48px;
  font-size: 18px;
}
.navdrop_p:hover {
  cursor: pointer;
  transition: 0.2s;
  color: #0082e6;
}
.arrow-icon:hover {
  color: #0082e6;
}
.arrow-button {
  border: none;
}
.arrow-icon {
  color: dimgray;
  background-color: transparent;
  border: none;
  transition: all 0.3s ease;
  cursor: pointer;
  font-size: 30px;
  &_navdrop {
    background-color: white;
    color: dimgray;
    border: none;
    transition: all 0.3s ease;
    cursor: pointer;
    font-size: 30px;
  }
}
